@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap");

.App {
  text-align: center;

  background: conic-gradient(
    from 180deg at 50% 20.72%,
    #f49c53 0deg,
    rgba(244, 209, 209, 0.83) 360deg
  );
  min-height: 100vh;
}

.tag {
  /* position: relative; */
  /* display: flex; */
  /* width: 918px; */
  /* left: 20vw; */
  text-align: center;
  padding-inline: 10vw;
  justify-content: center;

  font-family: "Poppins";

  font-weight: 700;
  font-size: 3vw;

  color: #754949b1;
}
.tag span {
  color: #c9620d;
}
.dash-btn {
  align-items: center;
  appearance: none;
  z-index: 10;

  /* margin: 10px; */
  background-image: radial-gradient(
    100% 100% at 100% 0,
    #5adaff 0,
    #5468ff 100%
  );
  border: 0;
  border-radius: 6px;
  box-shadow: rgba(45, 35, 66, 0.4) 0 2px 4px,
    rgba(45, 35, 66, 0.3) 0 7px 13px -3px, rgba(58, 65, 111, 0.5) 0 -3px 0 inset;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-flex;
  font-family: "JetBrains Mono", monospace;
  height: 48px;
  justify-content: center;
  line-height: 1;
  list-style: none;
  overflow: hidden;
  padding-left: 16px;
  padding-right: 16px;
  position: relative;
  text-align: left;
  text-decoration: none;
  transition: box-shadow 0.15s, transform 0.15s;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  will-change: box-shadow, transform;
  font-size: 18px;
}

.dash-btn:focus {
  box-shadow: #3c4fe0 0 0 0 1.5px inset, rgba(45, 35, 66, 0.4) 0 2px 4px,
    rgba(45, 35, 66, 0.3) 0 7px 13px -3px, #3c4fe0 0 -3px 0 inset;
}

.dash-btn:hover {
  box-shadow: rgba(45, 35, 66, 0.4) 0 4px 8px,
    rgba(45, 35, 66, 0.3) 0 7px 13px -3px, #3c4fe0 0 -3px 0 inset;
  transform: translateY(-2px);
}

.dash-btn:active {
  box-shadow: #3c4fe0 0 3px 7px inset;
  transform: translateY(2px);
}

@media screen and (max-width: 600px) {
  .tag {
    font-size: 2.3vh;
    padding-inline: 0px;
  }
  .tag button{
    position: absolute;
    top: 100rem;
  }
  .link-btn{
    position: relative;
    top: 7rem;
  }
  .people-ilus {
    scale: 0.5;
  }
  .woman {
    top: 50rem;
    left: 0.5rem;
  }
}
